@import './utils/index.scss';
@import './components/index.scss';
@import './layout/index.scss';
@import './overall.scss';

* {
  box-sizing: border-box;
}

html, body {
  font-size: 10px;
  font-family: $mainFont;
  font-weight: normal;
  background-color: $background1;
}

//-------------------------------------
// Скрывает слайдер с маленьких девайсов
.swiper-container {
  display: none;
  @media ($tablet) {
    display: flex;
  }
}

.sliderPreviewSites__title {
  display: none;
  @media ($tablet) {
    display: flex;
  }
}

.sliderPreviewSites__navigation {
  display: none;
  @media ($tablet) {
    display: flex;
  }    
}

.activeBlock {
  display: block;
}

.hideBlock {
  display: none;
}

.opacityShow {
  opacity: 1;
}

.container-nav {
  width: 100%;
  max-width: 125rem;
  margin: 0 auto;
}

.activeMenu {
  font-weight: 500;
  color: $colorLinksActive;
  // text-decoration: underline;
}

// COLORS
.bold {
  font-weight: 700;
}

