.container-lable {
    width: 100%;
    max-width: 125rem;
    padding: 0 2rem;
    margin: 0 auto;
    @media ($desktop) {
        padding: 0 2rem;
    }
}

.container-section {
    width: 100%;
    max-width: 114rem;
    margin: 0 auto;
}

.popup {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1000;
}