.label {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 4rem;
  margin-top: 6rem;
  @media ($phone) {
    margin-top: 0;
  }
  @media ($tablet) {
    flex-direction: row;
  }
  &__title {
    color: $colorLinksActive;
    font-size: 2.7rem;
    font-weight: 500;
    line-height: 4rem;
    letter-spacing: 0.03px;
    @media ($phone) {
      font-size: 3.6rem;
    }
  }
  &__input {
    width: 28rem;
    background: $backgroundSearch;
    border: none;
    border-radius: 4px;
    color: $grayPlaiceholder;
    font-size: 14px;
    margin: 1rem 0 2rem;
    padding: 1rem;
    box-sizing: border-box !important;
    @media ($phone) {
      width: 53rem;
    }
    @media ($tablet) {
      width: 20rem;
      margin: 0;
    }
    @media ($desktop) {
      width: 30rem;
    }
  }
}