// Появление основновного меню
.showMenu {
    animation: showMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.hideMenu {
    animation: hideMenu 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.opacityShow {
    animation-name: opacityShow;
    animation-duration: 0.3s;
}
   
.rotate-90-ccw {
    animation: rotate-90-ccw 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.rotate-anti-90 {
    animation: rotate-anti-90 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

// Сертификаты
.scale-up-ver-bottom {
    animation: scale-up-ver-bottom 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

// Окно POPUP с большим сертификатом
.scale-up-center {
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.scale-down-center {
    animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 1.940) both;
    //animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.fadeIn-opacity {
    animation-name: fadeIn-opacity;
    animation-duration: 1s;
    animation-fill-mode: both;
}


// Keyframes

// Всплытие Меню
@keyframes showMenu {
    0% {
      transform: scaleY(0.4);
      transform-origin: 100% 0%;
    }
    100% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
    }
}

@keyframes hideMenu {
    0% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
    }
    100% {
      transform: scaleY(0);
      transform-origin: 100% 0%;
    }
}

@keyframes opacityShow {
    0% {
      opacity: 0;
    }
    70% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

// Сертификаты
@keyframes scale-up-ver-bottom {
    0% {
        transform: scaleY(0.4);
        transform-origin: 0% 100%;
    }
    100% {
        transform: scaleY(1);
        transform-origin: 0% 100%;
    }
}

// Окно POPUP с большим сертификатом
@keyframes scale-up-center {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes scale-down-center {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.5);
    }
}

@keyframes fadeIn-opacity {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}