header {
   position: relative;
}

.pre-menu {
   position: absolute;
   bottom: 0;
   top: 0.9rem;
   right: 3rem;
   z-index: 999;
   @media ($tablet) {
      right: 7rem;
   }
}

.header {
   height: 6rem;
   &__nav {
      height: 16rem;
      align-items: center;
      @media ($phone) {
         height: 6rem;
      }
      @media ($projector) {
         padding: 0;
      }
   }
}
