.slogan {
   position: absolute;
   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 100%;
   height: 6rem;
   background: linear-gradient(90deg, #441d0b, #390d0c);
   margin: 0 auto;
   &__item {
      flex-basis: 33.3%;
   }
   &__title {
      color: white;
      font-size: 1.6rem;
      text-align: center;
   }
   &__link {
      @include link();
      display: none;
      @media ($phone) {
         display: inline;
      }
   }
   &__img {
      width: 3.8rem;
      height: 3.8rem;
      margin-left: 1rem;
      margin-right: 2.5rem;
      object-fit: cover;
      object-position: center;
      transform: scale(-1, 1) rotate(0.5turn);
      @media ($desktop) {
         margin-left: 13rem;
      }
   }
}

#codewars {
   width: 110px;
   @media ($tablet) {
      width: auto;
      height: 28px;
   }
}

.slogan-color {
   position: absolute;
   width: 100%;
   height: 7.5rem;
   background: linear-gradient(90deg, #c02d28, #e66125);
   margin: 0 auto;
   transform: rotate(-0.7deg);
}