.certificates {
    background-color: $backgroundFilter;
    margin-top: 3rem !important;
    padding: 0 5rem !important;
    &__items {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 1rem 0 5rem;
        @media ($tablet) {
            flex-direction: row;
        }
    }
    &__item {
        position: relative;
        flex-basis: calc(50% - 2rem);
        background-color: ghostwhite;
        cursor: pointer;
        margin-top: 4rem;
        overflow: hidden;
        &:nth-child(-n + 1) {
            margin-top: 0;
        }
        @media ($tablet) {
            &:nth-child(-n + 2) {
                margin-top: 0;
            }
        }
    }
    &__img {
        @include img();
        width: 100%;
    }
    &__popup {
        position: absolute;
        display: none;
        bottom: 0;
        height: 100%;
        background-color: $opacity4;
        color: white;
        padding: 2rem 4rem;
        cursor: auto;
        @media ($laptop) {
            height: 35%;
        }
    }
    &__title {
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 1rem;
    }
    &__subtitle {
        font-size: 1.8rem;
        font-weight: 500;
        margin-bottom: 1rem;
    }
    &__txt {
        font-size: 1.4rem;
    }
}

.imgCertificate {
    @include img();
    max-width: 65%;
}